import React from 'react'
import { Characters } from '../Characters'
import Cards from '../components/Cards'

const WomenScreem = () => {

    const womans = Characters.filter(women => women.type === "m") 
    

    return (
        <div>
            <div className='container mt-3'>
                <h2 className='text-center'>Womans</h2>
                <hr />
            </div>
            
            <div className='row justify-content-center'>
            {
                womans.map((women) => 
                    <Cards key={women.id} {...women}/>
                )
            }
            </div>
           
        </div>
    )
}

export default WomenScreem
